body {
    background:  url("/public/star-wars-background-full-hd-459613.jpg") center;
    height: 100vh;
    background-size: cover;
    color: #ffd700;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    margin: 0;
}
.bigBox{
    display: flex;
}
.container {
    text-align: start;
    width: 50%;
    padding: 20px;
    border: 2px #ffd700;
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.5);
}

h2 {
    font-size: 36px;
    margin-bottom: 20px;
    letter-spacing: 2px;
    text-shadow: 2px 2px 3px #000;
    transition: color 0.3s;
    border-bottom: 2px solid #ffd700;
}

ul {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, minmax(120px, 1fr));
    gap: 10px;
    justify-content: center;
}

button {
    background-color: #001f3f;
    color: #ffd700;
    border: 2px solid #ffd700;
    padding: 15px 25px;
    margin: 5px;
    cursor: pointer;
    font-size: 18px;
    transition: background-color 0.3s, color 0.3s, transform 0.3s;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
}

button:hover {
    background-color: #ffd700;
    color: #001f3f;
    transform: scale(1.1);
}

.description {
    display: flex;
    justify-content: space-between;
}

p {
    text-shadow: -3px -3px 3px black, 3px -3px 3px black, -3px 3px 3px black, 3px 3px 3px black;
    margin-top: 20px;
    font-size: 18px;
    transition: opacity 0.5s, transform 0.5s;
}

strong {
    color: #ffd700;
    position: relative;
    display: inline-block;
}

strong::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 50%;
    height: 2px;
    background-color: #ffd700;
}
.star-wars-body {
    background-color: rgba(0, 0, 0, 0.4); /* Dim background color */
    height: 100vh;
    overflow: hidden;
}

.story-container {
    width: 60%;
    margin: auto;
}

.crawl-container {
    position: relative;
    height: 100vh;
    font-family: 'Arial', sans-serif;
    color: yellow;
    font-size: 20px;
    line-height: 1.5;
    animation: crawl 60s linear infinite;
}
.story{
    font-size: 60px;
}
@keyframes crawl {
    0% {
        transform: translateY(100vh);
    }
    100% {
        transform: translateY(-150vh);
    }
}

@keyframes crawl {
    0% {
        transform: translateY(100vh);
    }
    100% {
        transform: translateY(-150vh);
    }
}

@media only screen and (max-width: 500px) {
    h2 {
        font-size: 20px;
    }

    button {
        padding: 8px 3px;
        font-size: 13px;
    }

    p {
        font-size: 13px;
    }
    .story {
        font-size: 13px;
    }
    ul {
        grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
    }
}